.m-card--image {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: calc(var(--spacer) / 2);

  .a-heading__last-word {
    padding-right: 0;
  }
}

.m-card--image__main {
  @extend %m-card__main;

  text-align: center;    
}

.m-card--image__title,
.m-card--image__title.below,
.m-card--image__title.above {
  @extend %m-card--bordered__title;

  h2 {
    text-align: center;
    color: var(--c-card--image-text);   
  }

  a {
    color: var(--c-card--image-text);
    
    &:hover {
      color: var(--c-card--image-text);
    }
  }

  svg {
    fill: var(--c-card--image-text);
  }
}

.m-card--image__image.highlight-below {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  border-radius: rem(10px);
  overflow: hidden;

  & > div {
    grid-column: 1;
    grid-row: 1;
  }

  .m-card--image__title {
    @extend %overlay-link-card;
    align-self: self-end;
    background-color: var(--c-card--image-highlight-below-bg);
  }

  .m-card--image__title h2 {
    padding: rem(13px) 0;
    color: var(--c-card--image-highlight-below-text);

    a {
      color: var(--c-card--image-highlight-below-text);
    }

    svg {
      fill: var(--c-card--image-highlight-below-text);
    }
  }
}

.m-card--image.m-card--bordered {
  .m-card--image__title {
    @extend %m-card--bordered__title;
    color: var(--c-card--bordered-illustration);
  }

  .m-card--image__description {
    @extend %m-card--bordered__description;
    color: var(--c-card--bordered-illustration);
  }
}